import Dropdown, { TYPES } from "../dropdown";
import MaskedTextField from "../masked-text-field";
import DateRange, { MODES } from "../date-range";
import ListOfCheckboxes from "../list-of-checkboxes";
import RateButton from "../rate-button";
import RangeSlider from "../range-slider";

export default class UiKitFormElements {
  static init() {
    const dropdown = new Dropdown({
      classId: "js-dropdown-ui-kit-1",
      type: TYPES.guests,
      defaultMessage: "Сколько гостей",
    });

    dropdown.start();

    MaskedTextField.start("js-mask-text-ui-kit");

    const dateRange1 = new DateRange({
      classId: "js-date-range-ui-kit-1",
      mode: MODES.double,
      onClean: () => {},
      onCalendarOpen: () => {},
      onSelected: () => {},
    });

    dateRange1.start();

    const dateRange2 = new DateRange({
      classId: "js-date-range-ui-kit-2",
      mode: MODES.single,
      onClean: () => {},
      onCalendarOpen: () => {},
      onSelected: () => {},
    });

    dateRange2.start();

    const rateButton1 = new RateButton({ classId: "js-rate-button-ui-kit-1" });
    rateButton1.start();

    const rateButton2 = new RateButton({ classId: "js-rate-button-ui-kit-2" });
    rateButton2.start();

    RangeSlider.start({
      classId: "js-range-slider-ui-kit",
      minValue: 5000,
      maxValue: 10000,
      step: 100,
      startValue: 6000,
      endValue: 9000,
    });

    const dropdown2 = new Dropdown({
      classId: "js-dropdown-ui-kit-2",
      type: TYPES.rooms,
      defaultMessage: "Сколько комнат",
    });

    dropdown2.start();

    const dropdown3 = new Dropdown({
      classId: "js-dropdown-ui-kit-3",
      type: TYPES.rooms,
      defaultMessage: "Сколько комнат",
    });

    dropdown3.start();
    dropdown3.showDropdown();

    const dropdown4 = new Dropdown({
      classId: "js-dropdown-ui-kit-4",
      type: TYPES.guests,
      defaultMessage: "Сколько гостей",
    });

    dropdown4.start();
    dropdown4.showDropdown();

    const dropdown5 = new Dropdown({
      classId: "js-dropdown-ui-kit-5",
      type: TYPES.guests,
      defaultMessage: "Сколько гостей",
    });

    dropdown5.start();
    dropdown5.showDropdown();

    const listOfCheckboxes2 = new ListOfCheckboxes("js-checkboxes-ui-kit-2");
    listOfCheckboxes2.start();
    listOfCheckboxes2.toggleContent();

    const listOfCheckboxes3 = new ListOfCheckboxes("js-checkboxes-ui-kit-3");
    listOfCheckboxes3.start();
  }
}
