import Carousel from "../carousel";

function formatPrice(value) {
  return value.toString()
    .split("")
    .reverse()
    .map((ch, index) => ((index + 1) % 3 === 0 ? ` ${ch}` : ch))
    .reverse()
    .join("");
}

export default class RoomItem {
  static init(index) {
    const priceEl = document.querySelector(
      `.js-carousel-${index} + .room-item__info .js-room-item__info-value`,
    );
    priceEl.innerHTML = formatPrice(priceEl.innerHTML);

    const carousel = new Carousel({ classId: `js-carousel-${index}` });
    carousel.start();
  }
}
