const DIRECTIONS = {
  left: "left",
  right: "right",
};

Object.freeze(DIRECTIONS);

class Carousel {
  constructor({ classId }) {
    this.classId = classId;

    this.leftBtnClass = "js-carousel__left-action";
    this.rightBtnClass = "js-carousel__right-action";
    this.carouselItemsClass = "js-carousel__items";
    this.carouselIndicatorClass = "js-carousel__indicator";
    this.indicatorActiveClass = "carousel__indicator_active";

    this.rootEl = document.querySelector(`.${classId}`);

    if (this.rootEl === null) { throw new Error(`Carousel error: El with classId ${classId} not found`); }
  }

  start() {
    this._initDom();
    this._initEvents();
    this._render();
  }

  _initDom() {
    this.leftBtnEl = this.rootEl.querySelector(`.${this.leftBtnClass}`);
    this.rightBtnEl = this.rootEl.querySelector(`.${this.rightBtnClass}`);

    this.indicatorsEls = this.rootEl.querySelectorAll(`.${this.carouselIndicatorClass}`);
    this.itemsEl = this.rootEl.querySelector(`.${this.carouselItemsClass}`);

    this.itemsCount = this.indicatorsEls.length;

    this.state = {
      currentSlidePosition: 0,
    };
  }

  _initEvents() {
    this.leftBtnEl.addEventListener("click", this._leftButtonHandler.bind(this));
    this.rightBtnEl.addEventListener("click", this._rightButtonHandler.bind(this));
  }

  _setState(newState) {
    this.state = newState;
    this._render();
  }

  _render() {
    const newPosition = 100 * this.state.currentSlidePosition;
    this.itemsEl.style.transform = `translateX(-${newPosition}%)`;

    for (let i = 0; i < this.itemsCount; i += 1) {
      this.indicatorsEls[i].classList.remove(this.indicatorActiveClass);
      if (i === this.state.currentSlidePosition) {
        this.indicatorsEls[i].classList.add(this.indicatorActiveClass);
      }
    }
  }

  _leftButtonHandler(e) {
    e.preventDefault();
    e.stopPropagation();

    this._slide(DIRECTIONS.left);
  }

  _rightButtonHandler(e) {
    e.preventDefault();
    e.stopPropagation();

    this._slide(DIRECTIONS.right);
  }

  _slide(direction) {
    let nextSlidePosition;
    if (direction === DIRECTIONS.right) {
      nextSlidePosition = this.state.currentSlidePosition + 1 >= this.itemsCount
        ? 0
        : this.state.currentSlidePosition + 1;
    }

    if (direction === DIRECTIONS.left) {
      nextSlidePosition = this.state.currentSlidePosition - 1 < 0
        ? this.itemsCount - 1 : this.state.currentSlidePosition - 1;
    }

    this._setState({ currentSlidePosition: nextSlidePosition });
  }
}

export default Carousel;
