import RoomSelectorForm from "../room-selector-form";
import RegisterForm from "../register-form";
import RoomDetailForm from "../room-detail-form";
import LoginForm from "../login-form";
import Calendar, { MODES } from "../calendar";
import RoomItem from "../room-item";

export default class UiKitCards {
  static init() {
    RoomSelectorForm.init();
    RegisterForm.init();

    const roomDetailForm = new RoomDetailForm({ classId: "js-detail-page-form" });
    roomDetailForm.start();
    LoginForm.init();

    /* eslint-disable no-new */
    new Calendar({
      classId: "js-ui-kit-calendar-cards",
      mode: MODES.single,
    });

    RoomItem.init(0);
    RoomItem.init(1);
  }
}
